import React, { Component } from "react";
import ScrollAnimation from "react-animate-on-scroll";
import act1 from "./act1.jpg";
import act2 from "./act2.jpg";
import act3 from "./act3.jpg";
import act4 from "./act4.jpg";
import act5 from "./act5.jpg";
import "./Activity.css";
class Activity extends Component {
  render() {
    return (
      <div className="container-fluid">
        <div id="actTitle" className="eduTitle">
          ACTIVITIES
        </div>
        <div className="row">
          <div id="eduLeftCol" className="col-6 px-0">
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="0">
              <div id="leftBlock">
                <p id="title">
                  2015-2016 <div id="dot">●</div>
                </p>
                <p id="subsubtitle">NTHU</p>
              </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="200">
              <div id="leftBlock-2">
                <p id="title">
                  2014-2015 <div id="dot">●</div>
                </p>

                <p id="subsubtitle">NTHU</p>
              </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="400">
              <div id="leftBlock-2">
                <p id="title">
                  2013-2014<div id="dot">●</div>
                </p>
                <p id="subsubtitle">NTHU</p>
              </div>
            </ScrollAnimation>
          </div>
          <div id="eduRightCol" className="col-6 px-0">
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="0">
              <div id="rightBlock">
                <p id="title"> Graduate Student Association</p>
                <p id="subsubtitle"> Event Planning</p>
              </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="200">
              <div id="actRightBlock-2">
                <p id="title">Student Association of PME Department</p>
                <p id="subsubtitle"> Event Planning</p>
              </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="400">
              <div id="actRightBlock-2">
                <p id="title">Pop Dancing Club</p>
                <p id="subsubtitle">Leader of Semester Performance</p>
              </div>
            </ScrollAnimation>
          </div>

          <div id="actCol" className="col-3 px-0 mx-0">
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="0">
              <img src={act1} className="container-fluid px-0 mx-0" />
            </ScrollAnimation>
          </div>

          <div id="actCol" className="col-3 px-0 mx-0">
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="200">
              <img src={act2} className="container-fluid px-0 mx-0" />
            </ScrollAnimation>
          </div>

          <div id="actCol" className="col-3 px-0 mx-0">
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="400">
              <img src={act3} className="container-fluid px-0 mx-0" />
            </ScrollAnimation>
          </div>

          <div id="actCol" className="col-3 px-0 mx-0">
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="600">
              <img src={act4} className="container-fluid px-0 mx-0" />
            </ScrollAnimation>
          </div>
        </div>
      </div>
    );
  }
}

export default Activity;
