import React, { Component } from "react";
import ScrollAnimation from "react-animate-on-scroll";
import "./Education.css";
class Education extends Component {
  render() {
    return (
      <div className="container-fluid">
        <div id="eduTitle" className="eduTitle">
          EDUCATION
        </div>
        <div className="row">
          <div id="eduLeftCol" className="col-6 px-0">
            <ScrollAnimation animateIn="fadeIn" duration="2">
              <div id="leftBlock">
                <p id="title">
                  2016-2018 <div id="dot">●</div>
                </p>
                <p id="subsubtitle">Master's Degree</p>
              </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="200">
              <div id="leftBlock-2">
                <p id="title">
                  2012-2016 <div id="dot">●</div>
                </p>

                <p id="subsubtitle">Bachelor's Degree</p>
              </div>
            </ScrollAnimation>
          </div>

          <div id="eduRightCol" className="col-6 px-0">
            <ScrollAnimation animateIn="fadeIn" duration="2">
              <div id="rightBlock">
                <p id="title">National Tsinghua University</p>
                <p id="subsubtitle">Electronics Engineering</p>
                <p id="subsubtitle">□ GPA 4.28/4.3</p>
              </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="200">
              <div id="rightBlock-2">
                <p id="title">National Tsinghua University</p>
                <p id="subsubtitle"> Power Mechanical Engineering</p>
                <p id="subsubtitle"> □ GPA 3.25/4.3</p>
              </div>
            </ScrollAnimation>
          </div>
        </div>
      </div>
    );
  }
}

export default Education;
