import React, { Component } from "react";
import ScrollAnimation from "react-animate-on-scroll";

import "./Experience.css";
class Experience extends Component {
  render() {
    return (
      <div className="container-fluid">
        <div id="expTitle" className="eduTitle">
          EXPERIENCE
        </div>
        <div className="row">
          <div id="eduLeftCol" className="col-6 px-0">
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="0">
              <div id="leftBlock">
                <p id="title">
                  2019 7/25-7/27 <div id="dot">●</div>
                </p>
                <p id="subtitle">In two days</p>
              </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="200">
              <div id="expleftBlock-2">
                <p id="title">
                  2018 12-2019 7 <div id="dot">●</div>
                </p>
              </div>
            </ScrollAnimation>
          </div>

          <div id="eduRightCol" className="col-6 px-0">
            <ScrollAnimation animateIn="fadeIn" duration="2">
              <div id="rightBlock">
                <p id="title">Resume Website</p>

                <p id="projectSubtitle">
                  □ Implemented and designed frontend pages with React,Bootstrap
                  and CSS
                </p>
                <p id="projectSubtitle">□ Deployed the application on Github</p>
              </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="200">
              <div id="rightBlock-2">
                <p id="title">Full-Stack Project: E-Commerce application</p>
                <a
                  id="projectLink"
                  href="http://ec2-18-217-34-9.us-east-2.compute.amazonaws.com"
                >
                  <p id="projectLink">
                    http://ec2-18-217-34-9.us-east-2.compute.amazonaws.com
                  </p>
                </a>
                <p id="projectSubtitle">
                  □ Implemented and designed frontend pages (shopping
                  cart,product and the
                </p>
                <p id="projectSubSubtitle">
                  checkout page) using React, Redux and Reactstrap
                </p>
                <p id="projectSubtitle">
                  □ Constructed Node.js backend server using Express and MongoDB
                </p>
                <p id="projectSubtitle">
                  □ Avoided callback hell using either Promise or Async/Await
                  function
                </p>
                <p id="projectSubtitle">
                  □ Implemented cash flow by integrating Third-Party Payment
                  (Ecpay){" "}
                </p>
                <p id="projectSubtitle">
                  □ Deployed the application on AWS and implemented proxy server
                  using Nginx
                </p>
              </div>
            </ScrollAnimation>
          </div>
        </div>
      </div>
    );
  }
}

export default Experience;
