import React, { Component } from "react";
import ScrollAnimation from "react-animate-on-scroll";
import "./Skills.css";
class Skills extends Component {
  render() {
    return (
      <div className="container-fluid">
        <div id="skiTitle" className="eduTitle">
          SKILLS
        </div>
        <div className="row">
          <div id="eduLeftCol" className="col-6 px-0">
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="0">
              <div id="skillTitle">
                <p>Node.js</p>
              </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="200">
              <div id="skillTitle">
                <p>Javascript</p>
              </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="400">
              <div id="skillTitle">
                <p>CSS</p>
              </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="600">
              <div id="skillTitle">
                <p>HTML</p>
              </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="800">
              <div id="skillTitle">
                <p>Nginx</p>
              </div>
            </ScrollAnimation>
          </div>
          <div id="eduRightCol" className="col-6 px-0">
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="0">
              <div id="skillTitle">
                <p>React.js</p>
              </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="200">
              <div id="skillTitle">
                <p>Python</p>
              </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="400">
              <div id="skillTitle">
                <p>Redux</p>
              </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="600">
              <div id="skillTitle">
                <p>MongoDB</p>
              </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeIn" duration="2" delay="800">
              <div id="skillTitle">
                <p>AWS</p>
              </div>
            </ScrollAnimation>
          </div>
        </div>
      </div>
    );
  }
}

export default Skills;
