import React, { Component } from "react";
import ScrollAnimation from "react-animate-on-scroll";
import profile from "./profile.jpg";
import "./Main.css";
class Main extends Component {
  render() {
    return (
      <div className="container-fluid">
        <div className="row">
          <div className="col-6 px-0">
            <img className="container-fluid px-0" src={profile} />
          </div>
          <div id="profile" className="col-6 px-0">
            <div id="subProfile">
              <ScrollAnimation
                animateIn="fadeIn"
                duration="2"
                delay="0"
                animateOnce="true"
              >
                <div>
                  <p id="name">Yi-Hong Shih</p>
                  <p id="subtitle">FRESH GRADUATE</p>
                </div>
              </ScrollAnimation>
              <ScrollAnimation
                animateIn="fadeIn"
                duration="2"
                delay="200"
                animateOnce="true"
              >
                <div id="subBlock">
                  <p id="title">Phone:</p>
                  <p id="subsubtitle">0958-425-380</p>
                </div>
              </ScrollAnimation>
              <ScrollAnimation
                animateIn="fadeIn"
                duration="2"
                delay="400"
                animateOnce="true"
              >
                <div id="subBlock">
                  <p id="title">Email:</p>
                  <p id="subsubtitle">shihyihong@gmail.com</p>
                </div>
              </ScrollAnimation>
              <ScrollAnimation
                animateIn="fadeIn"
                duration="2"
                delay="600"
                animateOnce="true"
              >
                <div id="subBlock">
                  <p id="title">Address:</p>
                  <p id="subsubtitle">
                    Songshan Dist., Taipei City 105, Taiwan
                  </p>
                </div>
              </ScrollAnimation>
              <ScrollAnimation
                animateIn="fadeIn"
                duration="2"
                delay="800"
                animateOnce="true"
              >
                <div id="subBlock">
                  <p id="title">Date of Birth:</p>
                  <p id="subsubtitle">November, 1992</p>
                </div>
              </ScrollAnimation>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Main;
